<template>
  <div class="grafik">
    <vx-card class="mb-base">
      <div class="md:flex">
        <div class="lg:w-1/4 mt-4 mb-4 md:mt-0 md:ml-6">
          <datepicker :minimumView="'month'" :maximumView="'year'" :format="'MMM yyyy'" v-model="selectedMonth" ></datepicker>
        </div>
        <div class="md:mt-0 md:mx-6">
          <vcheckboxpopup :label="'Entitas'" v-model="selected" :arrayList="entitas" style="display:inline;"></vcheckboxpopup>
        </div>   
        <div class="mt-4 md:mt-0">
          <vs-button  radius color="primary" @click="loadDataServer" icon="search" style="display:inline; margin-right:30px;"></vs-button>
        </div>    
      </div>
    </vx-card>

    <div class="w-full mb-base">
      <div class="vx-card overflow-hidden" v-show="selected > []">
        <div class="vx-card__header">
          <div class="vx-card__title">
            <h4 class="">Entitas</h4> 
          </div> 
        </div>
        <div class="vx-card__collapsible-content vs-con-loading__container">
          <div class="vx-card__body ">
            <div v-if="selected.length == entitas.length">
              <vs-chip class="mb-2" @click="selected.splice(index, 1)" closable color="primary" close-icon="close">
                All Entitas
              </vs-chip>
            </div>
            <ul v-else>
              <li v-for="(item ,index)  in (selected)" :key="index">
                <vs-chip class="mb-2" @click="selected.splice(index, 1)" closable color="primary" close-icon="close">
                  {{item.nama}}
                </vs-chip>
              </li>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <div v-show="activa_lancar > ''">
      <div class="vx-row">
        <div class="w-full lg:w-1/2 vx-col mb-base">
          <vx-card title="Balance Sheet" title-color="primary">
            <table class="w-full">
              <tr>
                <td>AKTIVA LANCAR</td>
                <td class="text-right">{{ activa_lancar }}</td>
              </tr>
              <tr>
                <td>AKTIVA TETAP</td>
                <td class="text-right">{{ activa_tetap }}</td>
              </tr>
              <tr>
                <td class="font-semibold pl-10">TOTAL AKTIVA</td>
                <td class="font-semibold text-right">{{ activa_total }}</td>
              </tr>
              <tr>
                <td class="pt-5"></td>
                <td class="pt-5"></td>
              </tr>
              <tr>
                <td>KEWAJIBAN LANCAR</td>
                <td class="text-right">{{ kewajiban_lancar }}</td>
              </tr>
              <tr>
                <td>KEWAJIBAN JANGKA PANJANG</td>
                <td class="text-right">{{ kewajiban_jangka_panjang }}</td>
              </tr>
              <tr>
                <td>EKUITAS</td>
                <td class="text-right">{{ ekuitas }}</td>
              </tr>
              <tr>
                <td class="font-semibold pl-10">TOTAL PASIVA</td>
                <td class="font-semibold text-right">{{ total_pasiva }}</td>
              </tr>
            </table>
          </vx-card>
        </div>
        <div class="w-full lg:w-1/2 vx-col mb-base">
          <vx-card >
            <GChart
              type="PieChart"
              :data="seriesCompanyPerfomance"
              :options="chartOptionsCompanyPerfomance"
            />
          </vx-card>
        </div>
      </div>

      <div class="vx-row">
        <div class="w-full lg:w-1/2 vx-col mb-base">
          <vx-card title="PROFIT & LOSS" title-color="primary">
            <table class="w-full">
                    <thead>
                      <tr class="bg-primary text-white">
                        <th class="bg-primary text-white">PROFIT & LOSS</th>
                        <th class="bg-primary text-white text-right">NILAI</th>
                        <th class="bg-primary text-white text-right">%</th>
                      </tr>
                    </thead>
                    <tbody>
                      <tr>
                        <td>SALES</td>
                        <td class="text-right">{{ sales_value }}</td>
                        <td class="text-right">{{ sales_persen }}%</td>
                      </tr>
                      <tr>
                        <td>HPP</td>
                        <td class="text-right">{{ hpp_value }}</td>
                        <td class="text-right">{{ hpp_persen }}%</td>
                      </tr>
                      <tr>
                        <td>GROSS MARGIN</td>
                        <td class="text-right">{{ gross_margin_value }}</td>
                        <td class="text-right">{{ gross_margin_persen }}%</td>
                      </tr>
                      <tr>
                        <td class="pt-5"></td>
                        <td class="pt-5"></td>
                        <td claßss="pt-5"></td>
                      </tr>
                      <tr>
                        <td>Beban operasi</td>
                        <td class="text-right">{{ beban_operasi_value }}</td>
                        <td class="text-right">{{ beban_operasi_persen }}%</td>
                      </tr>
                      <tr>
                        <td>Laba(Rugi)Bersih(Before tax)</td>
                        <td class="text-right">{{ laba_rugi_value }}</td>
                        <td class="text-right">{{ laba_rugi_persen }}%</td>
                      </tr>
                    </tbody>
            </table>
          </vx-card>
        </div>
        <div class="w-full lg:w-1/2 vx-col mb-base">
          <vx-card >
            <apexchart ref="updateChartPersen" height="190" :options="pieChartPersen" :series="seriespiePersen"></apexchart>
          </vx-card>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'
import VueApexCharts from 'vue-apexcharts'
import { GChart } from 'vue-google-charts'
import vcheckboxpopup from '../components/CheckboxPopup'

export default {
  data() {
    return {
      popupActivo:false,
      entitas:[],
      selected: [],
      selectedMonth:new Date(),
      activa_lancar:'',
      activa_lancar_persen:'',
      activa_tetap:'',
      activa_tetap_persen:'',
      activa_total:'',
      kewajiban_lancar:'',
      kewajiban_lancar_persen:'',
      kewajiban_jangka_panjang:'',
      kewajiban_jangka_panjang_persen:'',
      ekuitas:'',
      ekuitas_persen:'',
      total_pasiva:'',
      sales_value:'',
      hpp_value:'',
      gross_margin_value:'',
      beban_operasi_value:'',
      laba_rugi_value:'',
      sales_persen:'',
      hpp_persen:'',
      gross_margin_persen:'',
      beban_operasi_persen:'',
      laba_rugi_persen:'',
      chartOptionsCompanyPerfomance: {
        title: 'HISANA CORP NASIONAL',
        is3D: true,
        // width: 400,
        // height: 240,
        colors:['#e4ba28','#cf91a8','#9b83bf','#a3b38f','#f6a549']
      },
      seriesCompanyPerfomance:null,
      pieChartPersen:{
        chart: {
          type: 'donut',

        },
        responsive: [{
          breakpoint: 480,
          options: {
            chart: {
              width: 400
            },
            legend: {
              position: 'bottom'
            }
          }
        }],
        labels:[],
        colors:['#5f9dd8','#f17c2f','#a6a6a6','#fec300'],
        title: {
          text: '%',
          align:'center'
        },
      },
      seriespiePersen:[],
    }
  },
  
  components: {
    Datepicker,
    apexchart: VueApexCharts,
    GChart,
    vcheckboxpopup
  },

  mounted() {
    this.loadEntitas()
  },

  methods: {
    loadEntitas() {
      this.entitas = this.$store.state.AppActiveUser.entitas
    },
    
    loadDataServer() {
      if (this.selected.length == 0) {
        this.$swal.fire(
          'Oopss !',
          'Anda Belum Memilih Entitas',
          'error'
        )
      } else {
        this.$vs.loading()
        let bulanPost = btoa(this.selectedMonth);
        var entitasId = this.selected.map(x => x.kode);
        let entitasPost = {
          kode_entitas : entitasId,
        }
        
        this.$http.post('neraca/grafik/'+ bulanPost, entitasPost)
        .then(response => {
          if(response.data.status == "SUKSES"){
            // Grafik Chart Omzet  
            this.$refs.updateChartPersen.updateSeries(
              response.data.message.seriespiePersen
            )
            this.$refs.updateChartPersen.updateOptions({
        
              labels: response.data.message.labels
            })

            this. seriesCompanyPerfomance = response.data.message.seriesCompanyPerfomance
            this.activa_lancar = response.data.message.activa_lancar
            this.activa_lancar_persen = response.data.message.activa_lancar_persen
            this.activa_tetap = response.data.message.activa_tetap
            this.activa_tetap = response.data.message.activa_tetap
            this.activa_total = response.data.message.activa_total
            this.kewajiban_lancar = response.data.message.kewajiban_lancar
            this.kewajiban_lancar_persen = response.data.message.kewajiban_lancar_persen
            this.kewajiban_jangka_panjang = response.data.message.kewajiban_jangka_panjang
            this.kewajiban_jangka_panjang_persen = response.data.message.kewajiban_jangka_panjang_persen
            this.ekuitas = response.data.message.ekuitas
            this.ekuitas_persen = response.data.message.ekuitas_persen
            this.total_pasiva = response.data.message.total_pasiva
            this.sales_value= response.data.message.sales_value
            this.hpp_value = response.data.message.hpp_value
            this.gross_margin_value = response.data.message.gross_margin_value
            this.beban_operasi_value = response.data.message.beban_operasi_value
            this.laba_rugi_value = response.data.message.laba_rugi_value
            this.sales_persen = response.data.message.sales_persen
            this.hpp_persen = response.data.message.hpp_persen
            this.gross_margin_persen = response.data.message.gross_margin_persen
            this.beban_operasi_persen = response.data.message.beban_operasi_persen
            this.laba_rugi_persen = response.data.message.laba_rugi_persen
          }
        })
        .catch(err=>{
            this.$vs.loading.close()
            this.$swal.fire(
              'Gagal !',
              err.response.data.message,
              'error'
            )
        })
        .finally(() => {this.$vs.loading.close()})
      }
    }
  }
}
</script>

<style scoped>
table {
  border-collapse: collapse;
}
</style>